import './App.css';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import React, { useState } from 'react'
// æˆ–
//import { Input } from '@mui/material';
const api_base_url = 'http://ls44982hw6.herokuapp.com/api'


function App() {
  const [firstname, setFirstname] = useState('');
  const [lastname, setLastname] = useState('');
  function find() {
    var url_req_string = api_base_url + "?firstname=" + firstname;//+ firstname;   
    console.log(url_req_string);
    fetch(url_req_string).then((res)=>{
      if(res.ok){
          res.text().then((data)=>{
              setLastname(data)
              console.log(data);
          })
      }
  }).catch((res)=>{
      console.log(res.status);
  });

      // .then(res => res.json())
      // .then(
      //   (result) => {
      //     setLastname(result)
      //     console.log(result);
      //   },
      //   (error) => {
      //     console.log(error);
      //   }
      // );
  }
  return (
    <div className="App">
      <header className="App-header">
        {/*  <img src={logo} className="App-logo" alt="logo" /> */}
        <TextField id="standard-basic" label="Please input first name" variant="standard" value={firstname} onChange={(event) => { setFirstname(event.target.value) }} />
        <Button variant="contained" sx={{ marginTop: 10 }} onClick={find}>Find Last Name</Button>
        <Box component="span" sx={{ p: 2, border: '10px dashed grey' }}>
          {lastname}
        </Box>
      </header>
    </div>
  );
}

export default App;
